/*==全局样式==*/
* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
div,
dl,
dt,
dd,
form,
h1,
h2,
h3,
h4,
h5,
h6,
img,
ol,
ul,
li,
table,
th,
td,
p,
span,
a {
  border: 0;
}
img,
input {
  border: none;
  vertical-align: middle;
}
body {
  font-family: Tahoma, Arial, Helvetica, "\5FAE\8F6F\96C5\9ED1";
  font-size: 14px;
  text-align: left;
  background: #FFF;
  color: #333;
  background-color: #f5f6f6;
}
html {
  overflow-y: scroll;
}
ul,
ol {
  list-style-type: none;
}
th,
td,
input {
  font-size: 12px;
}
h3 {
  font-size: 14px;
}
button {
  border: none;
  cursor: pointer;
  font-size: 12px;
  background-color: transparent;
}
select {
  border-width: 1px;
  _zoom: 1;
  border-style: solid;
  padding-top: 2px;
  font-size: 12px;
}
a:link,
a:visited {
  text-decoration: none;
  color: #333;
}
a:hover,
a:active {
  text-decoration: underline;
  color: #f60;
}
.clear:after {
  content: ' ';
  clear: both;
  display: block;
  height: 0;
  visibility: hidden;
}
.clear {
  zoom: 1;
}
#app {
  width: 100%;
  height: 100%;
}
.content {
  width: 960px;
  height: auto;
  margin: 0 auto;
}
.pagination {
  width: 100%;
